<template>
  <div id="qrcode" class="container">
    <br/>
    <h1>{{ msg }}</h1>
    <br/>
    <div class="row">
      <textarea id="textarea" class="form-control" v-model="qrstring" placeholder="input qrcode info"></textarea>
      <button id="generateButton" class="btn btn-primary btn-sm" v-on:click="generate">生成二维码</button>
      <canvas id="canvas"></canvas>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode'

export default {
  name: 'QRCode',
  data () {
    return {
      msg: '二维码生成器',
      qrstring: 'orztools.com'
    }
  },
  mounted () {
    QRCode.toCanvas(document.getElementById('canvas'), this.qrstring, function (error) {
      if (error) console.error(error)
      console.log('success!')
    }
    )
  },
  methods: {
    generate: function (event) {
      QRCode.toCanvas(document.getElementById('canvas'), this.qrstring, function (error) {
        if (error) console.error(error)
        console.log('success!')
      }
      )
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#qrcode {
  background-color: #fff;
}

textarea {
    width: 55%!important;
    float: left;
    outline: 0;
    height: 260px;
    resize: none;
    font-size: 16px;
    padding: 24px;
    position: relative;
}

#generateButton {
  margin-left: 20px
}

#canvas {
  margin-left: 20px;
}

h1, h2 {
  font-weight: normal;
}
</style>



