<template>
  <div class="hello">
    <br/>
    <h1>{{ msg }}</h1>

    <br/>
    <h3>开发者</h3>
    <ul class="list-group list-group-horizontal-sm">
      <li class="list-group-item" v-on:click="navItemClick('json')">JSON编辑器</li>
      <li class="list-group-item" v-on:click="navItemClick('qrcode')">二维码生成器</li>
    </ul>

    <br/>

    <h3>单位转换</h3>
    <ul class="list-group list-group-horizontal-sm">
      <li class="list-group-item" v-on:click="navItemClick('length')">长度转换</li>
      <li class="list-group-item" v-on:click="navItemClick('length')">时间转换</li>
      <li class="list-group-item" v-on:click="navItemClick('color')">颜色转换</li>
    </ul>

    <br/>
    <img src="../assets/logo.jpg">
  </div>
</template>

<script>

export default {
  name: 'Index',
  components: {
  },
  data () {
    return {
      msg: 'Welcome to ORZtools',
      active: 'home'
    }
  },
  methods: {
    navItemClick: function (item) {
      if (this.active === item) {
        return
      }
      this.active = item
      if (this.active === 'home') {
        this.$router.push('/')
      } else if (this.active === 'json') {
        this.$router.push('/JsonEditor')
      } else if (this.active === 'qrcode') {
        this.$router.push('/QRCode')
      } else if (this.active === 'length') {
        this.$router.push('/Length')
      } else if (this.active === 'color') {
        this.$router.push('/ColorConversion')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  margin: 20px;
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



