<template>
  <div class="length">
    <br/>
    <h1>{{ msg }}</h1>

    <br/>
    <div class="input-group">
      <input type="text" class="form-control" aria-label="Text input with dropdown button" v-model="originValue">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Dropdown</button>
        <div class="dropdown-menu">
          <a class="dropdown-item" href="#">Action</a>
          <a class="dropdown-item" href="#">Another action</a>
          <a class="dropdown-item" href="#">Something else here</a>
          <div role="separator" class="dropdown-divider"></div>
          <a class="dropdown-item" href="#">Separated link</a>
        </div>
      </div>
      <button class="btn btn-primary btn-sm">=</button>
      <input type="text" class="form-control" aria-label="Text input with dropdown button" v-model="resultValue">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Dropdown</button>
        <div class="dropdown-menu">
          <a class="dropdown-item" href="#">Action</a>
          <a class="dropdown-item" href="#">Another action</a>
          <a class="dropdown-item" href="#">Something else here</a>
          <div role="separator" class="dropdown-divider"></div>
          <a class="dropdown-item" href="#">Separated link</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import convert from 'convert-units'

export default {
  name: 'Length',
  components: {
  },
  data () {
    return {
      msg: '长度转换',
      originValue: 1,
      resultValue: 1000
    }
  },
  mounted () {
    this.resultValue = convert(this.originValue).from('l').to('ml')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
</style>



